html,
body {
  padding: 0;
  margin: 0;
  height: fit-content;
}

a {
  color: inherit;
  text-decoration: none;
}

* {
  box-sizing: border-box;
}

div#wentilab-wave {
  position: relative;
  text-align: center;
  width: 100%;
  height: 100px;
  margin: 0 auto;
  margin-top: 30px;
}
div#wentilab-wave .dot {
  display: inline-block;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  margin-right: 8px;
  background: black;
  animation: wave 1.3s linear infinite;
}
div#wentilab-wave .dot:nth-child(2) {
  animation-delay: -1.1s;
}
div#wentilab-wave .dot:nth-child(3) {
  animation-delay: -0.9s;
}

@keyframes wave {
  0%, 60%, 100% {
    transform: initial;
  }
  30% {
    transform: translateY(-15px);
  }
}